import React, { useContext, useEffect } from 'react';

//IMPORTING CSS FILE
import '../styles.css/ForPages/Designs.css';

//IMPORTING DESIGNS CONTEXT
import DesignsContext from '../context/DesignsContext';

//IMPORTING COMPONENTS
import DesignCard from '../components/DesignCard';
import Loading from '../pages/Loading';

export default function Skills() {
  const { getDesigns, designsList, loading } = useContext(DesignsContext);

  useEffect(() => {
    getDesigns();
    // eslint-disable-next-line
  }, []);
  return (
    <>
      <div id="design_cards_container">
        {designsList &&
          designsList.map((design) => {
            return (
              <DesignCard
                key={design._id}
                id={design._id}
                description={design.description}
                title={design.title}
                image={design.image}
              />
            );
          })}
      </div>
      {loading && <Loading />}
    </>
  );
}
