import React, { useState } from 'react';
import DesignsContext from './DesignsContext';
// import jwt from 'jsonwebtoken';
import { useEffect } from 'react';

export default function DesignsState(props) {
  const [designsList, setDesignsList] = useState([]);
  const [specificDesign, setSpecificDesign] = useState(null);
  const [isAdmin, setIsAdmin] = useState(false);

  const [adminTitle, setAdminTitle] = useState('Design');
  const [adminDescription, setAdminDescription] = useState(
    'Our CNC wood design files are created using state-of-the- computer-aided design (CAD) software, resulting inrecise andintricate designs that can be manufactured using a computernumerical control (CNC) machine. These designs can be used to createa wide.'
  );
  const [adminImage, setAdminImage] = useState(
    'https://thumbs.dreamstime.com/b/no-image-available-icon-vector-illustration-flat-design-140476186.jpg'
  );

  const [designId, setDesignId] = useState('');

  const [loading, setLoading] = useState(false);
  const [progress, setProgress] = useState(0);

  useEffect(() => {
    checkAdmin();
  }, []);

  async function checkAdmin() {
    // localStorage.setItem('intermediate', '@testing-library/jest-dom');
    const token = localStorage.getItem(
      '5baa61e4c9b93f3f0682250b6cf8331b7ee68fd8'
    );
    if (!token || token !== '5f4dcc3b5aa765d61d8327deb882cf99') {
      return;
    }
    setIsAdmin(true);
  }

  async function getDesigns() {
    try {
      const url = `https://axiomatic-winter-lute.glitch.me/api/v1/designs/alldesigns`;
      setProgress(30);
      setLoading(true);
      const design = await fetch(url, {
        method: 'get',
        headers: {
          'Content-Type': 'application/json',
          token: 'token_token',
        },
      });
      setProgress(70);
      const jsonDesigns = await design.json();
      setDesignsList(jsonDesigns);
      setLoading(false);
      setProgress(100);
    } catch (error) {
      console.log(error);
    }
  }

  async function getSpecificDesign(id) {
    try {
      const url = `https://axiomatic-winter-lute.glitch.me/api/v1/designs/getdesign/${id}`;
      const specificdesign = await fetch(url, {
        method: 'get',
        headers: {
          'Content-Type': 'application/json',
          token: 'token_token',
        },
      });
      const jsonSpecificDesign = await specificdesign.json();
      console.log(jsonSpecificDesign);
      setSpecificDesign(jsonSpecificDesign);
    } catch (error) {
      console.log(error);
    }
  }

  async function addDesign(design) {
    try {
      const url = `https://axiomatic-winter-lute.glitch.me/api/v1/designs/adddesign`;
      const addeddesign = await fetch(url, {
        method: 'post',
        headers: {
          'Content-Type': 'application/json',
          token: 'token_token',
        },
        body: JSON.stringify(design),
      });
      const jsonAddedDesign = await addeddesign.json();
      if (jsonAddedDesign.success) {
        setDesignsList(designsList.concat(jsonAddedDesign.savedDesign));
        console.log(jsonAddedDesign.success);
      }
      console.log(jsonAddedDesign);
    } catch (error) {
      console.log(error);
    }
  }

  async function updateDesign(id, design) {
    try {
      const url = `https://axiomatic-winter-lute.glitch.me/api/v1/designs/updatedesign/${id}`;
      const updatedDesign = await fetch(url, {
        method: 'put',
        headers: {
          'Content-Type': 'application/json',
          token: 'token_token',
        },
        body: JSON.stringify(design),
      });
      const jsonUpdatedDesign = await updatedDesign.json();
      if (jsonUpdatedDesign.success) {
        setAdminTitle('');
        setAdminDescription('');
        setAdminImage('');
      }
      console.log(jsonUpdatedDesign);
    } catch (error) {
      console.log(error);
    }
  }

  async function deleteDesign(id) {
    try {
      const url = `https://axiomatic-winter-lute.glitch.me/api/v1/designs/deletedesign/${id}`;
      const deletedDesign = await fetch(url, {
        method: 'delete',
        headers: {
          'Content-Type': 'application/json',
          token: 'token_token',
        },
      });
      const jsondeletedDesign = await deletedDesign.json();
      if (jsondeletedDesign.success) {
        const newDesignList = designsList.filter((design) => {
          return design._id !== jsondeletedDesign.deletedDesign._id;
        });
        setDesignsList(newDesignList);
      }
      return true;
    } catch (error) {
      console.log(error);
    }
  }

  return (
    <DesignsContext.Provider
      value={{
        designsList,
        specificDesign,
        isAdmin,
        adminTitle,
        adminDescription,
        adminImage,
        designId,
        loading,
        progress,
        setProgress,
        setDesignId,
        setAdminTitle,
        setAdminDescription,
        setAdminImage,
        setDesignsList,
        setSpecificDesign,
        getDesigns,
        getSpecificDesign,
        addDesign,
        updateDesign,
        deleteDesign,
      }}
    >
      {props.children}
    </DesignsContext.Provider>
  );
}
