import React, { useContext, useEffect, useState } from 'react';
import { Link, useParams, useNavigate } from 'react-router-dom';

//IMPORTING CSS FILE
import '../styles.css/ForPages/DesignDetail.css';

//IMPORTING ICONS FROM MATERIAL UI
import BrowserUpdatedIcon from '@mui/icons-material/BrowserUpdated';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import FacebookIcon from '@mui/icons-material/Facebook';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import InstagramIcon from '@mui/icons-material/Instagram';

//IMPORTING DESIGNSLIST
import DesignsContext from '../context/DesignsContext';

export default function DesignDetail() {
  const [title, setTitle] = useState('Design');
  const [description, setDescription] = useState(
    'Our CNC wood design files are created using state-of-the- computer-aided design (CAD) software, resulting inrecise andintricate designs that can be manufactured using a computernumerical control (CNC) machine. These designs can be used to createa wide.'
  );
  const [image, setImage] = useState(null);

  const {
    getSpecificDesign,
    specificDesign,
    isAdmin,
    deleteDesign,
    setAdminTitle,
    setAdminDescription,
    setAdminImage,
    setDesignId,
  } = useContext(DesignsContext);

  const navigate = useNavigate();

  function imageBigger() {
    const src = document.getElementById('imagesrc');
    const fullImageDiv = document.getElementById('fullImageDiv');
    const fullImage = document.getElementById('fullImage');

    fullImageDiv.style.display = 'block';
    console.log(src);
    fullImage.src = specificDesign && specificDesign.image;
  }
  function imageCloser() {
    // const src = document.getElementById('imagesrc');
    const fullImageDiv = document.getElementById('fullImageDiv');
    const fullImage = document.getElementById('fullImage');

    fullImageDiv.style.display = 'none';
    console.log(fullImage.src);
  }

  function updateHandler(e) {
    e.preventDefault();
    setAdminTitle(title);
    setAdminDescription(description);
    setAdminImage(image);
    setDesignId(id);
    navigate('/admin/update');
  }

  const params = useParams();
  const id = params.id;

  useEffect(
    () => {
      getSpecificDesign(id);
      setTitle(specificDesign && specificDesign.title);
      setDescription(specificDesign && specificDesign.description);
      setImage(specificDesign && specificDesign.image);
    },
    // eslint-disable-next-line
    [id]
  );

  return (
    <section id="design_detail">
      <div id="main_design_detail">
        {isAdmin && (
          <div id="delete_update_icons_design_card">
            <BrowserUpdatedIcon onClick={updateHandler} />
            <DeleteForeverIcon
              onClick={async (e) => {
                e.preventDefault();
                console.log(' delete clicked');
                const hasDesignDeleted = await deleteDesign(id);
                hasDesignDeleted && navigate('/designs');
              }}
            />
          </div>
        )}
        <div id="detail_page_col1">
          <div id="detail_page_image">
            <img
              src={specificDesign && specificDesign.image}
              alt="This"
              id="imagesrc"
              onClick={(e) => {
                e.preventDefault();
                imageBigger();
              }}
            />
          </div>
          {/* <h1>{specificDesign && specificDesign.title}</h1> */}
        </div>
        <div id="detail_page_col2">
          <h1>Description</h1>
          <p>{specificDesign && specificDesign.description}</p>
          <p>
            So If you want to buy this design odder me on
            {
              <Link
                to={
                  'http://api.whatsapp.com/send?phone=+923421413220text=welcome'
                }
                style={{ color: 'black', marginTop: '12px' }}
              >
                __WhatsApp
                <WhatsAppIcon /> .
              </Link>
            }
          </p>
          <FacebookIcon
            style={{
              marginInline: '2.5px',
              marginBlock: '15px',
              color: '#233150',
            }}
          />
          <InstagramIcon
            style={{
              marginInline: '2.5px',
              marginBlock: '15px',
              color: '#233150',
            }}
          />
          <MailOutlineIcon
            style={{
              marginInline: '2.5px',
              marginBlock: '15px',
              color: '#233150',
            }}
          />
          <WhatsAppIcon
            style={{
              marginInline: '2.5px',
              marginBlock: '15px',
              color: '#233150',
            }}
          />
        </div>
      </div>

      <div
        id="fullImageDiv"
        onClick={(e) => {
          e.preventDefault();
          imageCloser();
        }}
      >
        <div id="fullImage_container">
          <img id="fullImage" alt="This" />
        </div>
      </div>
    </section>
  );
}
