import React, { useContext } from 'react';

//IMPORTING CSS FILE
import '../styles.css/ForComponents/DesignCard.css';

import { Link, useNavigate } from 'react-router-dom';
import DesignsContext from '../context/DesignsContext';

//IMPORTING ICONS FROM MATERIAL UI
import BrowserUpdatedIcon from '@mui/icons-material/BrowserUpdated';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';

export default function DesignCard({ image, title, id, description }) {
  const {
    isAdmin,
    deleteDesign,
    setAdminTitle,
    setAdminDescription,
    setAdminImage,
    setDesignId,
  } = useContext(DesignsContext);

  const navigate = useNavigate();

  const styles = {
    height: '210px',
    backgroundImage: `url(${image})`,
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    borderRadius: '0 0 10px 10px',
  };

  function updateHandler(e) {
    e.preventDefault();
    setAdminTitle(title);
    setAdminDescription(description);
    setAdminImage(image);
    setDesignId(id);
    navigate('/admin/update');
  }

  return (
    <div id="card">
      {isAdmin && (
        <div id="delete_update_icons_design_card">
          <BrowserUpdatedIcon onClick={updateHandler} />
          <DeleteForeverIcon
            onClick={(e) => {
              e.preventDefault();
              console.log(' delete clicked');
              deleteDesign(id);
            }}
          />
        </div>
      )}
      <Link to={`/designs/${id}`}>
        <div id="card_pic" style={styles}></div>
        <div id="card_content">
          <h1>{title}</h1>
        </div>
      </Link>
    </div>
  );
}
