import React, { useContext } from 'react';
import { Link, useNavigate } from 'react-router-dom';

//IMPORTTING MATERIAL UI ICONS

import WhatsAppIcon from '@mui/icons-material/WhatsApp';

//IMPORTING CSS FILE
import '../styles.css/ForPages/Admin.css';

//IMPORTING DESIGNS CONTEXT
import DesignsContext from '../context/DesignsContext';

export default function Admin() {
  const {
    addDesign,
    // getDesigns,
    adminTitle,
    adminDescription,
    adminImage,
    setAdminTitle,
    setAdminDescription,
    setAdminImage,
  } = useContext(DesignsContext);

  const design = {
    title: adminTitle,
    description: adminDescription,
    image: adminImage,
  };
  const navigate = useNavigate();

  return (
    <section id="admin_page">
      {/* FOR PREVIEW */}
      <div id="preview">
        <div id="preview_image_h1">
          <div id="preview_image">
            <img src={adminImage} alt="This" />
          </div>
          <div id="preview_h1" style={{ marginInline: 'auto ' }}>
            <h1>{adminTitle}</h1>
          </div>
        </div>
        <div id="preview_description">
          <h1>Description</h1>
          <p>{adminDescription}</p>
          <p>
            So If you want to buy this design odder me on
            {
              <Link
                to={
                  'http://api.whatsapp.com/send?phone=+923421413220text=welcome'
                }
                style={{ color: 'black', marginTop: '12px' }}
              >
                __WhatsApp
                <WhatsAppIcon /> .
              </Link>
            }
          </p>
        </div>
      </div>
      {/* FOR INPUTS */}
      <div id="inputs">
        <input
          type="text"
          name="title"
          placeholder="Enter The Title"
          onChange={(e) => {
            setAdminTitle(e.target.value);
          }}
        />
        <textarea
          rows={8}
          type="text"
          name="description"
          placeholder="Enter The Description"
          onChange={(e) => {
            setAdminDescription(e.target.value);
          }}
        />
        <input
          type="url"
          autoComplete="off"
          name="image"
          placeholder="Enter The Image Url"
          onChange={(e) => {
            setAdminImage(e.target.value);
          }}
        />
        <button
          id="design_submit_button"
          onClick={() => {
            addDesign(design);
            navigate('/');
          }}
        >
          Publish
        </button>
      </div>
    </section>
  );
}
